/* src/app/features/rooms/rooms-page.component.css */
.rooms-container {
  padding: 3rem 1rem;
}
.rooms-list {
  margin-top: 2.25rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}
.room-card {
  position: relative;
  background:
    linear-gradient(
      150deg,
      rgba(26, 35, 50, 1) 0%,
      rgba(15, 23, 36, 1) 95%);
  padding: 1.75rem 1.5rem;
  border-radius: 16px;
  color: #fff;
  border: 1px solid var(--border);
  box-shadow: 0 10px 28px -6px rgba(0, 0, 0, .45);
  transition:
    transform .35s ease,
    box-shadow .35s ease,
    border-color .35s;
  overflow: hidden;
}
.room-card:before {
  content: "";
  position: absolute;
  inset: 0;
  background:
    radial-gradient(
      circle at 80% 20%,
      rgba(0, 194, 138, 0.18),
      transparent 60%);
}
.room-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 16px 46px -10px rgba(0, 0, 0, .55);
  border-color: var(--brand-accent);
}
.room-card h3 {
  margin: 0 0 .6rem;
  font-size: 1.2rem;
  font-weight: 600;
}
.room-card p {
  margin: 0 0 1rem;
  font-size: .95rem;
  line-height: 1.55;
  color: var(--text-secondary);
}
.room-actions {
  margin-top: .75rem;
}
.room-actions button {
  background:
    linear-gradient(
      90deg,
      #00C28A,
      #06B6D4);
  color: #fff;
  border: none;
  padding: .6rem 1.1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  width: 100%;
  transition: filter .25s;
}
.room-actions button:hover {
  filter: brightness(1.12);
}
/*# sourceMappingURL=rooms-page.component.css.map */
